import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'

import { handleLogout } from '../../config/utils'
import Header from '../../components/fixed/header'
import BlockedProfiles from './blockedProfiles'
import ChangePassword from './changePassword'
import DeleteAccount from './deleteAccount'

export default function Settings(props) {
    if (!props.page) props.page = 'blocked-profiles'

    document.title = 'Configuraes / Napker'

    useEffect(() => {
        document.getElementById(props.page).classList.add('active')
    }, [])

    const pageChange = page => {
        document.querySelectorAll('.active').forEach(el => {
            el.classList.remove('active')
        })
        document.getElementById(page).classList.add('active')
    }

    return (
        <>
            <Header page="Configuraes" />
            <div className="content">
                <div className="settings-page-container">
                    <div className="settings-page-menu">
                        <div className="d-flex flex-column w-100">
                            <Link to="/configuraes/perfis-bloqueados" style={{ textDecoration: 'none', width: '100%' }}>
                                <li id="blocked-profiles" onClick={() => pageChange('blocked-profiles')}>
                                    Perfis bloqueados
                            <i className="fas fa-angle-right"></i>
                                </li>
                            </Link>
                            <Link to="/configuraes/alterar-senha" style={{ textDecoration: 'none', width: '100%' }}>
                                <li id="change-password" onClick={() => pageChange('change-password')}>
                                    Alterar senha
                            <i className="fas fa-angle-right"></i>
                                </li>
                            </Link>
                            <Link to="/configuraes/deletar-conta" style={{ textDecoration: 'none', width: '100%' }}>
                                <li id="delete-account" onClick={() => pageChange('delete-account')}>
                                    Apagar conta
                            <i className="fas fa-angle-right"></i>
                                </li>
                            </Link>
                        </div>
                        <li className="logout-btn" onClick={handleLogout}>
                            Sair da conta
                        </li>
                    </div>
                    {props.page === 'blocked-profiles' ?
                        <BlockedProfiles />
                        :
                        <>
                            {props.page === 'change-password' ?
                                <ChangePassword />
                                :
                                <DeleteAccount />
                            }
                        </>
                    }
                </div>
            </div>
        </>
    )
}